.main-container {
  display: flex;
}

.rightrail {
  position: sticky;
  top: 1rem;
  height: fit-content;
  padding-top: 1rem;
}

@media (max-width: 1024px) {
  .main-container {
    flex-direction: column;
  }

  .rightrail {
    width: 100% !important;
    position: static;
  }
}